* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  min-width: 1280px;
}
html, body, #root {
  width: 100%;
  height: 100%;
}
li {
  list-style: none;
}
a {
  color : #000;
  text-decoration : none;
}
em, i {
  font-style: normal;
}
.ivv-header {
    width: 100%;
    height: 65px;
    padding: 0 20px;
    background-color: #F8F8F8 !important;
    border-bottom: 1px solid #eee;
}
.ivv-header .header {
    display: flex;
    width: 90%;
    height: 100%;
    margin: 0 auto;
    justify-content: space-between;
}
.ivv-header .ivv-logo {
    display: flex;
    height: 60px;
    align-items: center;
}
.ivv-header .ivv-logo .logo {
    width: 40px;
    height: 40px;
}
.ivv-header .ivv-logo  .logo-letter {
    color: #2F4056;
    font-size: 26px;
}
/* nav */
.ivv-header .ivv-nav {
    display: flex;
    width: 200px;
    height: 60px;
    align-items: center;
    justify-content: space-between;
}
.ivv-header .ivv-nav .active {
    color: #20a0ff;
}

.ivv-vform {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, .6)
}
.ivv-vform .vform {
    display: flex;
    width: 450px;
    padding-top: 30px;
    flex-direction: column;
    background-color: #fff;
    border-radius: 8px;
}
.ivv-vform .vform li{
    display: flex;
    width: 100%;
    height: 50px;
    justify-content: center;
    align-items: center;
}
.ivv-vform .vform li .vform-name{
    display: flex;
    min-width: 90px;
    padding-right: 10px;
    flex-direction: row-reverse;
}
.ivv-vform .vform li .vform-module{
    position: relative;
    display: flex;
    width: 200px;
    flex-wrap: wrap;
}
.ivv-vform .vform li .vform-module .vform-module-add {
    position: absolute;
    top: 10px;
    left: 205px;
    color: #FF4949;
}
.ivv-vform .vform li .vform-module .vform-module-add:hover{ 
    cursor: pointer;
}
.ivv-vform .vform li.vform-form-opt{
    height: auto;
    align-items: stretch;
}
.vform .vform-form-opt .vform-module-opt{
    position: relative;
}
.vform .vform-form-opt .vform-module-opt .vform-module-reduce {
    position: absolute;
    top: 10px;
    left: 205px;
    color: #20a0ff;
}
.vform .vform-form-opt .vform-module-opt .vform-module-reduce:hover{
    cursor: pointer;
}
.ivv-vform .vform li.vform-form-opt .el-input{
    margin-bottom: 10px;
}
.ivv-vform .vform li .vform-opt {
    display: flex;
    min-width: 90px;
    height: 36px;
    padding-right: 10px;
    flex-direction: row-reverse;
    align-items: center;
}
.ivv-vform .vform .lastli {
    padding-right: 30px;
    margin-bottom: 20px;
    justify-content: flex-end;
}


.ivv-login {
    width: 90%;
    height: calc(100% - 65px);
    min-height: 500px;
    margin: 0 auto;
    background-color: #F8F8F8;
}
.ivv-login .login {
    display: flex;
    width: 300px;
    height: 100%;
    margin: 0 auto;
    flex-wrap: wrap;
    align-content: center;
}
.ivv-login .login .login-label {
    display:flex;
    width: 300px;
    height: 60px;
    align-items: center;
    justify-content: flex-end;
}
.ivv-login .login .login-label .login-label-name {
    display: flex;
    width: 100px;
    padding-right: 15px;
    flex-direction: row-reverse;  
}
.ivv-login .login .login-label .login-label-input {
    width: 200px;
}
.ivv-menusider {
    width: 260px;
    height: 100%;
    background-color: #F8F8F8;
    border-right: 1px solid #eee;
}
.ivv-menusider ul {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
}
.ivv-menusider ul li {
    display: flex;
    width: 100%;
    height: 60px;
    justify-content: center;
    align-items: flex-end;
}
.ivv-menusider .active {
    color: #20a0ff;
}
.ivv-loading-form {
    width: calc(100% - 260px);
    min-height: 500px;
    overflow-x: hidden;
    overflow-y: scroll;
}
.ivv-loading-form::-webkit-scrollbar {
    width: 12px;
    background-color: #eee;
}
.ivv-loading-form::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #111;
}
.ivv-form {
    display: flex;
    width: 100%;
    padding: 25px;
    flex-direction: column;
}
.ivv-form .ivv-table-title {
    display: flex;
    width: 800px;
    margin-top: 10px;
    justify-content: space-around;
}
.ivv-form .ivv-table-title li {
    display: flex;
    width: 100px;
    justify-content: center;
}
.ivv-form .ivv-table-title li:last-child {
    width: 200px;
}
.ivv-form .ivv-table-field {
    display: flex;
    width: 800px;
    margin-top: 10px;
    justify-content: space-around;
}
.ivv-form .ivv-table-dot {
    border: 1px dashed #000;
}
.ivv-form .ivv-table-field li {
    display: flex;
    width: 100px;
    justify-content: center;
}
.ivv-form .ivv-table-field li:last-child {
    width: 200px;
    justify-content: space-around;
}
.ivv-form .ivv-custom span{ 
    display: inline-block;
    width: 150px;
    margin-top: 40px;
}
.ivv-form .ivv-field-add {
    color: #20a0ff;
}
.ivv-form .ivv-field-add:hover {
    cursor: pointer;
}
.ivv-table-field .ivv-form-edit{
    color: #20a0ff;
}
.ivv-table-field .ivv-form-edit:hover {
    cursor: pointer;
}
.ivv-table-field .ivv-form-del{
    color: #FF5722;
}
.ivv-table-field .ivv-form-del:hover {
    cursor: pointer;
}
.ivv-table-field .ivv-form-up {
    color: #58B7FF;
}
.ivv-table-field .ivv-form-up:hover {
    cursor: pointer;
}
.ivv-table-field .ivv-form-down {
    color: #1D8CE0;
}
.ivv-table-field .ivv-form-down {
    cursor: pointer;
}
.ivv-form .ivv-save {
    display: flex;
    margin-top: 30px;
    padding-right: 30px;
    justify-content: flex-end;
}
.accounts {
    display: flex;
    width: calc(100% - 260px);
    justify-content: center;
    align-items: center;
}
.accounts-layout {
    width: 400px;
    padding: 15px;
}
.accounts-layout li {
    display: flex;
    width: 100%;
    height: 50px;
    align-items: center;
}
.accounts-layout li:last-child {
    justify-content: flex-end;
}
.accounts-layout .accounts-name {
    display: flex;
    width: 150px;
    padding-right: 15px;
    justify-content: flex-end;
}
.accounts-layout .accounts-input {
    width: 200px;
}
.content{
    width: 100%;
    height: 100%;
    background: #f1f1f1;
    padding:0 5%;
}
.ivv-detail {
    /* background: white; */
}

.ivv-detail .ivv-detail-ul {
  display: flex;
  flex-direction: column;
}

.ivv-detail .ivv-detail-ul .ivv-detail-li {
  display: flex;
  flex-wrap: pre-wrap;
}

.ivv-detail .ivv-detail-name {
  width: 150px;
  text-align: right;
  margin-top: 15px;
  padding-right: 15px;
  color: grey;
}

.ivv-detail .ivv-detail-value {
  display: flex;
  white-space: pre-wrap;
  padding-left: 15px;
  margin-top: 15px;
  /* color: #20a0ff; */
}

.title {
  height: 70px;
  display: flex;
  align-items: center;
  padding-left: 80px;
  font-size: 25px;
  border-bottom: 1px solid gainsboro;
}

.title>span {
  margin-right: 20px;
}
.ivv-edit {
    display: flex;
    width: 90%;
    min-height: 500px;
    margin: 15px auto;
    padding: 15px;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: center;
    background-color: #F8F8F8;
    border-radius: 8px;
}
.ivv-edit .ivv-edit-ul {
    display: flex;
    width: 700px;
    flex-direction: column;
    background-color: transparent;
}
.ivv-edit .ivv-edit-ul .ivv-edit-li{
    display: flex;
    padding: 10px 0;
    flex-wrap: nowrap;
    align-items: center;
}
.ivv-edit .ivv-edit-name {
    display: flex;
    width: 150px;
    padding-right: 15px;
    justify-content: flex-end;
}
.ivv-edit .ivv-edit-name i {
    color: red;
}
.ivv-edit .ivv-edit-input {
    display: flex;
    padding-left: 15px;
}
.ivv-edit .ivv-edit-input .ivv-edit-textarea {
    width: 500px;
}
.ivv-edit .ivv-edit-primary {
    display: flex;
    width: 600px;
    justify-content: flex-end;
}
.App {
    width: 100%;
    height: 100%;
}
.ivv-table {
    display: flex;
    width: 90%;
    margin: 5px auto;
    justify-content: center;
}
.table-loading {
    width: 100%;
    height: 100%
}
.ivv-pagination {
    display: flex;
    width: 90%;
    margin: 25px auto;
    flex-direction: row-reverse;
}
.table-nav{
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    height: 77px;
}
.table-nav .el-input{
    width: 150px;
    margin: 0 20px;
}
.ivv-router {
    width: 100%;
    height: 100%;
}
.ivv-content {
    display: flex;
    width: 90%;
    margin: 0 auto;
    height: calc(100% - 90px);
    min-height: 500px;
    background-color: #F8F8F8;
}
.ivv-field {
    display: flex;
    width: calc(100% - 300px);  
    padding: 10px 20px;
    flex-direction: column;
}
